@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial:  12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

/* Whitespace Reset */
* {
  padding: 0;
  margin: 0; }

/* Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1024px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

@media screen and (min-width: 1900px) {
  html {
    font-size: 20px; } }

body, table, th, td, form, input, textarea, option, select {
