@-webkit-keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.3rem);
    transform: translate(0, -0.3rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.3rem);
    transform: translate(0, -0.3rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

form {
  -webkit-animation: shake 0.5s 1 ease-in-out forwards;
  animation: shake 0.5s 1 ease-in-out forwards; }

fieldset,
fieldset legend {
  border: 2px solid #DDD;
  border-radius: 0.5em;
  -webkit-box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.4); }

fieldset {
  background: #FFF;
  padding: 3.5vh 4.5vw 3vh 4vw; }

fieldset legend {
  display: inline-block;
  background: #FFF;
  padding: 0.6em 2.2em;
  font-weight: bold; }

label {
  display: block;
  font-weight: bold; }

input[type="text"],
input[type="password"] {
  display: block;
  width: 20rem;
  border: 2px solid #BBB;
  padding: 0.4em 1em;
  border-radius: 0.2rem;
  background: #EEE;
  outline: none;
  -webkit-transition: background 0.5s ease, border-color 0.5s ease;
  transition: background 0.5s ease, border-color 0.5s ease; }

input[type="text"]:focus,
input[type="password"]:focus {
  background: #FFF;
  border-color: #333; }

input[placeholder="username"],
input[placeholder="********"] {
  font-style: italic; }

input[type="submit"] {
  display: block;
  cursor: pointer;
  border: 2px solid #CCC;
  background: #333;
  color: #FFF;
  padding: 0.6em 2.2em;
  border-radius: 0.4rem;
  font-weight: bold; }

input[type="submit"]:hover {
  background: #060;
  -webkit-transition: background 0.5s ease;
  transition: background 0.5s ease;
  -webkit-box-shadow: 0 0 1rem 0 rgba(1, 182, 1, 0.4);
  box-shadow: 0 0 1rem 0 rgba(1, 182, 1, 0.4); }

