  background: #EEE;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column; }

body.login .site-header {
  display: none; }

.navigation {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9); }

.navigation ul {
  padding: 0;
  margin: 0; }

.navigation li {
  display: block;
  padding: 1em 0;
  list-style: none outside;
  opacity: 0;
  -webkit-animation: navitemin 0.5s 1 ease forwards;
  animation: navitemin 0.5s 1 ease forwards;
  -webkit-transform: scale(0.1) translate(0, 3rem) rotate(720deg);
  transform: scale(0.1) translate(0, 3rem) rotate(720deg); }

.navigation a {
  padding: 1.5em 2.5em;
  display: block;
  font-size: 1.5em;
  text-decoration: none;
  background: #fff;
  color: #444;
  border-radius: 0.5rem;
  -webkit-box-shadow: 0 0 1.5rem 0 rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 1.5rem 0 rgba(0, 0, 0, 0.3); }

.navigation li:nth-child(1) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

.navigation li:nth-child(2) {
  -webkit-animation-delay: 0.30s;
  animation-delay: 0.30s; }

.navigation li:nth-child(3) {
  -webkit-animation-delay: 0.45s;
  animation-delay: 0.45s; }

.navigation li:nth-child(4) {
  -webkit-animation-delay: 0.60s;
  animation-delay: 0.60s; }

.navigation li:nth-child(5) {
  -webkit-animation-delay: 0.75s;
  animation-delay: 0.75s; }

.navigation li:nth-child(6) {
  -webkit-animation-delay: 0.90s;
  animation-delay: 0.90s; }

.navigation a:before {
  margin-right: 1em; }

#nav-main-toggle {
  display: none; }

.nav-toggle {
  position: fixed;
  top: var(--spacing-site, 2rem);
  left: var(--spacing-site, 2rem);
  z-index: 1000;
  font-size: 2rem;
  padding: 0 0.5rem;
  -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
  animation: navtogglein 0.5s 1 ease-in-out forwards; }

#nav-toggle-right {
  left: inherit;
  right: var(--spacing-site, 2rem); }

.nav-toggle span {
  display: none; }

#nav-toggle-right {
  left: inherit;
  right: var(--spacing-site, 2rem); }

.nav-toggle:before {
  margin-right: 1rem;
  /* unicode to icon awesome  equivalent -> "fas fa-bars" */
  content: '\f0c9'; }

.nav-toggle:before {
  margin-right: 1rem; }

#nav-main-toggle:checked + .site-header .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1000; }

#nav-main-toggle:checked + .site-header .nav-toggle:before {
  /* unicode to icon awesome equivalent -> "fas fa-times"*/
  content: '\f00d'; }

#nav-main-toggle:checked + .site-header .nav-toggle {
  z-index: 1010;
  -webkit-animation: jump 0.1s 1 ease-in-out forwards;
  animation: jump 0.1s 1 ease-in-out forwards; }

.listing {
